import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import { useCallback } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import ReadyToPublish from "./ReadyToPublish/ReadyToPublish";
const PublishConfig = ({ ioid, staffId, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/member?a=ioward`
        : `/q/${params.username}/member?a=ioward`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("publish")}</h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/member?a=ioward&type=published${tab.urlKey}`
                : `/q/${params.username}/member?a=ioward&type=published${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />

      {searchParams.get("n") === "sent" ? null : (
        <ReadyToPublish ioid={ioid} staffId={staffId} />
      )}
    </>
  );
};

export default PublishConfig;
const tabList = [
  {
    label: "ready_to_publish",
    id: "ready_to_publish",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "published_on_feed",
    id: "feed",
    urlKey: "&n=feed",
    groupIs: ["feed"],
  },
  {
    label: "sent_via_mail",
    id: "mail",
    urlKey: "&n=mail",
    groupIs: ["mail"],
  },
  {
    label: "sent_via_letter",
    id: "lettet",
    urlKey: "&n=lettet",
    groupIs: ["lettet"],
  },
];
