import { useTranslation } from "react-i18next";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import InOutwardSentRequestPending from "./InOutwardSentRequestPending";
import InOutwardSentRequestApproved from "./InOutwardSentRequestApproved";

const InOutwardSentRequestConfig = ({ staffId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();

  return (
    <>
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("d"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/member?a=ioward&type=request&n=sent${tab.urlKey}`
                : `/q/${params.username}/member?a=ioward&type=request&n=sent${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.2rem",
        }}
      >
        {searchParams.get("d") === "approved" ? (
          <InOutwardSentRequestApproved staffId={staffId} />
        ) : (
          <InOutwardSentRequestPending staffId={staffId} />
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default InOutwardSentRequestConfig;
const tabList = [
  {
    label: "pending",
    id: "pending",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "approved",
    id: "approved",
    urlKey: "&d=approved",
    groupIs: ["approved"],
  },
];
