import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import InOutwardReceiveRequestPendingCard from "./InOutwardReceiveRequestPendingCard";
import { useInoutwardRecieveRequestPendingList } from "../../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
const InOutwardReceiveRequestPending = ({ staffId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    inoutwardRecieveRequestPendingList,
    inoutwardRecieveRequestPendingListLoading,
    inoutwardRecieveRequestPendingListRefetch,
  } = useInoutwardRecieveRequestPendingList({
    data: {
      sid: staffId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !staffId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (inoutwardRecieveRequestPendingList?.list?.length) {
      let obj = nextPage(inoutwardRecieveRequestPendingList?.list?.length);
      setState(() => obj);
    }
  }, [inoutwardRecieveRequestPendingList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !inoutwardRecieveRequestPendingListLoading &&
      inoutwardRecieveRequestPendingList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    inoutwardRecieveRequestPendingListRefetch();
  }, [inoutwardRecieveRequestPendingListRefetch]);

  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>
      {inoutwardRecieveRequestPendingList?.list?.map((outward, index) =>
        inoutwardRecieveRequestPendingList?.list?.length === index + 1 ? (
          <div key={outward?._id} ref={ref}>
            <InOutwardReceiveRequestPendingCard
              outward={outward}
              onRefetch={onRefetch}
              staffId={staffId}
            />
          </div>
        ) : (
          <InOutwardReceiveRequestPendingCard
            outward={outward}
            key={outward?._id}
            onRefetch={onRefetch}
            staffId={staffId}
          />
        )
      )}
      {inoutwardRecieveRequestPendingListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        inoutwardRecieveRequestPendingList?.list?.length === 0 && (
          <EmptyMessage
            title="No item found related this search."
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
          />
        )
      ) : (
        inoutwardRecieveRequestPendingList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No item."
          />
        )
      )}
    </>
  );
};

export default InOutwardReceiveRequestPending;
