import { useTranslation } from "react-i18next";
import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import InOutwardManagerProfile from "./InOutwardManagerProfile";
import { Suspense } from "react";
import { useInoutwardMaster } from "../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import InOutwardRequestConfig from "./InOutwardRequest/InOutwardRequestConfig";
import Outward from "./Outward/Outward";
import Inward from "./Inward/Inward";
import PublishConfig from "./Publish/PublishConfig";
import InOutwardDetail from "./InOutwardDetail/InOutwardDetail";

const InOutwardManager = ({ instituteId, staffId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { inoutwardMaster } = useInoutwardMaster({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  return (
    <>
      <UserMemberMainWrapper>
        <UserMemberContainerWrapper>
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("type") ? (
              <InOutwardManagerProfile
                instituteId={instituteId}
                staffId={staffId}
              />
            ) : searchParams.get("type") === "request" ? (
              <InOutwardRequestConfig
                instituteId={instituteId}
                staffId={staffId}
              />
            ) : searchParams.get("type") === "outward" ? (
              <Outward
                ioid={inoutwardMaster?._id}
                instituteId={instituteId}
                staffId={staffId}
              />
            ) : searchParams.get("type") === "inward" ? (
              <Inward
                ioid={inoutwardMaster?._id}
                instituteId={instituteId}
                staffId={staffId}
              />
            ) : searchParams.get("type") === "published" ? (
              <PublishConfig
                ioid={inoutwardMaster?._id}
                instituteId={instituteId}
                staffId={staffId}
              />
            ) : searchParams.get("type") === "ward-detail" ? (
              <InOutwardDetail staffId={staffId} />
            ) : null}
          </Suspense>
        </UserMemberContainerWrapper>
      </UserMemberMainWrapper>
    </>
  );
};

export default InOutwardManager;
