import { useTranslation } from "react-i18next";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { Link, useLocation, useParams } from "react-router-dom";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";

const InOutwardSentRequestApprovedCard = ({ outward }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <>
      <Link
        className={style.sf_card}
        to={`/q/${params.username}/member?a=ioward&type=ward-detail`}
        state={{
          ...getQuery.state,
          outward,
          isLastShow: outward?.model_type === "INWARD" ? "Inward" : "Outward",
        }}
      >
        <div
          className={style.sf_card_contatiner}
          style={{
            alignItems: "flex-start",
          }}
        >
          <div className={style.sf_card_contatiner_inner}>
            {outward?.model_type === "INWARD" ? (
              <>
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("name")}
                  {": "}
                  {outward?.name ?? ""}
                </h6>
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("no")}
                  {": "}
                  {outward?.number ?? ""}
                </h6>
              </>
            ) : (
              <>
                {" "}
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("subject_only")}
                  {": "}
                  {outward?.subject ?? ""}
                </h6>
              </>
            )}
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("type")}
              {": "}
              {outward?.outward_type ?? ""}
            </h6>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("approval_for")} {":"}
            </h6>
            {outward?.approvals_for?.map((ap, index) => (
              <h6
                key={`${index}${ap?.staff?._id}`}
                style={{
                  fontSize: "13px",
                }}
              >
                {`${ap?.staff?.staffFirstName ?? ""} ${
                  ap?.staff?.staffMiddleName ?? ""
                } ${ap?.staff?.staffLastName ?? ""}`}
                {" - "}
                <span
                  style={{
                    color: "#00f",
                  }}
                >
                  ({`${ap?.status ?? ""}`})
                </span>
              </h6>
            ))}
            <p>
              {t("created_at")} :{" "}
              {dayjs(outward?.created_at)?.format("DD/MM/YYYY")} {", "}
              {localTimeFormat(outward?.created_at, "LT")}
            </p>
          </div>
        </div>
        <div
          className={style.sf_card_contatiner}
          style={{
            flexDirection: "column",
          }}
        >
          {outward?.generated_report ? (
            <a
              href={`${pdfCdnUrl}/${outward?.generated_report}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src={`${assestsUserFinanceUrl}/receipt.svg`}
                alt="receipt icon"
                style={{
                  width: "1.5rem",
                  height: "1.5rem",
                }}
              />
            </a>
          ) : null}
        </div>
      </Link>
    </>
  );
};

export default InOutwardSentRequestApprovedCard;
