import { useEffect, useState } from "react";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import FormFile from "../../../Ui/Input/FormFile";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import InstituteStaff from "../../InstituteStaff/InstituteStaff";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import { useInoutwardCreateInOutward } from "../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
const AddInward = ({
  staffId,
  instituteId,
  onClose,
  ioid,
  onRefetch,
  isEdit,
  menuData,
  onEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [state, setState] = useState({
    outward_type: "",
    name: "",
    number: "",
    date: "",
    attachment: [],
    approvals_for: [],
    approvals_forId: [],
  });

  const [fileUpload] = useFileUpload();
  const [inoutwardCreateInOutward] = useInoutwardCreateInOutward();

  useEffect(() => {
    if (menuData?._id && isEdit) {
      setState((prev) => ({
        ...prev,
        outward_type: menuData?.outward_type ?? "",
        name: menuData?.name ?? "",
        number: menuData?.number ?? "",
        date: menuData?.date ? slashDateFormat(menuData?.date) : "",
        attachment: menuData?.attachment ?? [],
      }));
    }
  }, [menuData, isEdit]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onApprovalChange = (e, pty) => {
    let inp = [...state.approvals_for];
    inp[pty]["designation"] = e.target.value;
    setState((prev) => ({
      ...prev,
      approvals_for: inp,
    }));
  };
  const onDateSelect = (val) => {
    setState((prev) => ({
      ...prev,
      date: slashDateFormat(val),
    }));
  };
  const onCloseStep = () => {
    setStep("");
  };
  const onFileUpload = (e, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [flow]: [...prev[flow], res.data?.imageKey],
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onStaffSelect = (val) => {
    if (state.approvals_forId?.includes(val?._id)) {
      setState((prev) => ({
        ...prev,
        approvals_for: prev?.approvals_for?.filter(
          (dt) => dt?.staff?._id !== val?._id
        ),
        approvals_forId: prev?.approvals_forId?.filter((dt) => dt !== val?._id),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        approvals_for: [
          ...prev?.approvals_for,
          {
            staff: val,
            designation: "",
          },
        ],
        approvals_forId: [...prev?.approvals_forId, val?._id],
      }));
    }
  };
  //   console.info("sdvfhvds", state);
  const onOutwardType = (val) => {
    setState((prev) => ({
      ...prev,
      outward_type: val,
    }));
  };

  const onAddInward = () => {
    let validation = requiredField(state, [
      "attachment",
      "approvals_forId",
      "approvals_for",
      "number",
    ]);
    if (isEdit) {
      if (existingRequiredField(validation)) {
        onEdit({
          outward_type: state.outward_type,
          name: state.name,
          number: state.number,
          date: ymdSlashDateFormat(state.date),
          attachment: state.attachment,
        });
      } else {
        setErrorField(validation);
      }
    } else {
      if (existingRequiredField(validation) && ioid && staffId) {
        setDisabled((pre) => !pre);
        let art = [];
        for (let dt of state.approvals_for) {
          art.push({
            staff: dt?.staff?._id,
            designation: dt?.designation,
          });
        }

        inoutwardCreateInOutward({
          ioid: ioid,
          bodyParameter: {
            outward_type: state.outward_type,
            name: state.name,
            number: state.number,
            date: ymdSlashDateFormat(state.date),
            attachment: state.attachment,
            prepare_by: staffId,
            approvals_for: art,
            isInward: "ADD_MANUAL",
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      } else {
        setErrorField(validation);
      }
    }
  };
  const onExistingFileUpload = (e, flow, pty) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          let dgt = [...state[flow]];
          dgt[pty] = res.data?.imageKey;
          setState((prev) => ({
            ...prev,
            [flow]: dgt,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <InstituteStaff
          title="select_approval_for"
          onClose={onCloseStep}
          instituteId={instituteId}
          isMultiSelect
          onMultiSelect={onStaffSelect}
          selectList={state.approvals_forId}
          onDone={onCloseStep}
        />
      ) : (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6> {t(isEdit ? "update_inward" : "add_inward")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <TileWrapper
                label="inward_type"
                errorShow={errorField["outward_type"]}
              >
                {outward_type_label?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onOutwardType}
                    activeTile={state.outward_type}
                  />
                ))}
              </TileWrapper>
              <FormInput
                label={t("inward_name")}
                placeholder={t("inward_name_placeholder")}
                name={"name"}
                value={state.name}
                type={"text"}
                onChange={onInputChange}
                errorShow={errorField["name"]}
              />
              <FormInput
                label={t("inward_number")}
                placeholder={t("inward_number_placeholder")}
                name={"number"}
                value={state.number}
                type={"text"}
                onChange={onInputChange}
              />
              <FormCalendarInput
                label={t("date")}
                placeholder={t("select_date")}
                name="calendar"
                value={state.date}
                onChange={onDateSelect}
                errorShow={errorField["date"]}
              />
              {isEdit ? null : (
                <>
                  <div
                    className={style.modal_container_outer_header}
                    style={{
                      marginTop: "1rem",
                    }}
                  >
                    <h6> {t("approval_for")}</h6>
                    <CreateButton
                      label="select_approval_staff"
                      customStyle={{
                        margin: "0",
                        padding: "0.3rem 1rem",
                      }}
                      onAction={() => setStep(1)}
                    />
                  </div>

                  {state.approvals_for?.map((fdt, index) => (
                    <div
                      className={style.modal_container_header}
                      key={`${index}${fdt?.staff}`}
                    >
                      <FormInput
                        label={t("approval_staff")}
                        name={`${index}${fdt?.staff}`}
                        value={`${fdt?.staff?.staffFirstName ?? ""} ${
                          fdt?.staff?.staffMiddleName ?? ""
                        } ${fdt?.staff?.staffLastName ?? ""}`}
                        disabled
                        customFormInputContainer={{
                          width: "49%",
                        }}
                      />
                      <FormInput
                        label={t("approval_staff_designation")}
                        placeholder={t(
                          "approval_staff_designation_placeholder"
                        )}
                        name={"subject"}
                        value={fdt.designation}
                        type={"text"}
                        onChange={(e) => onApprovalChange(e, index)}
                        customFormInputContainer={{
                          width: "49%",
                        }}
                      />
                    </div>
                  ))}
                </>
              )}
              {state.attachment?.length > 0 ? (
                <>
                  {state.attachment?.map((ifg, index) => (
                    <FormFile
                      key={ifg}
                      label={t("add_image")}
                      name={`attacment${ifg}${index}`}
                      value={{
                        key: ifg,
                        name: "View",
                      }}
                      onChange={(e) =>
                        onExistingFileUpload(e, "attachment", index)
                      }
                    />
                  ))}
                  <FormFile
                    label={t("add_attachment")}
                    name={"attacment"}
                    onChange={(e) => onFileUpload(e, "attachment")}
                  />
                </>
              ) : (
                <FormFile
                  label={t("add_attachment")}
                  name={"attacment"}
                  onChange={(e) => onFileUpload(e, "attachment")}
                />
              )}

              <CreateButton
                label={isEdit ? "update" : "create"}
                onAction={onAddInward}
              />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddInward;
export const outward_type_label = [
  {
    key: "STUDENT",
    name: "student",
  },
  {
    key: "PARENT",
    name: "parents",
  },
  {
    key: "OTHER",
    name: "other",
  },
];
