import { useTranslation } from "react-i18next";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
// import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import {
  useInoutwardApprovedOutward,
  useInoutwardRejectOutward,
} from "../../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import { useState } from "react";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { Link, useLocation, useParams } from "react-router-dom";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import InOutwardSentRequestPendingCardMenu from "../Sent/InOutwardSentRequestPendingCardMenu";

const InOutwardReceiveRequestPendingCard = ({
  outward,
  onRefetch,
  staffId,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const getQuery = useLocation();
  const params = useParams();
  const [step, setStep] = useState("");
  const [inoutwardApprovedOutward] = useInoutwardApprovedOutward();
  const [inoutwardRejectOutward] = useInoutwardRejectOutward();

  const onClose = () => {
    setStep("");
  };

  const onApproved = (e) => {
    e.preventDefault();
    if (outward?._id && staffId) {
      setDisabled((pre) => !pre);
      inoutwardApprovedOutward({
        sid: staffId,
        oid: outward?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onReject = (e) => {
    e.preventDefault();
    if (outward?._id && staffId) {
      setDisabled((pre) => !pre);
      inoutwardRejectOutward({
        sid: staffId,
        oid: outward?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onMenu = (e) => {
    e.preventDefault();
    setStep(1);
  };
  return (
    <>
      <Link
        className={style.sf_card}
        to={`/q/${params.username}/member?a=ioward&type=ward-detail`}
        state={{
          ...getQuery.state,
          outward,
          isLastShow: outward?.model_type === "INWARD" ? "Inward" : "Outward",
          isApproved: "YES",
        }}
      >
        <div
          className={style.sf_card_contatiner}
          style={{
            alignItems: "flex-start",
          }}
        >
          <div className={style.sf_card_contatiner_inner}>
            {outward?.model_type === "INWARD" ? (
              <>
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("name")}
                  {": "}
                  {outward?.name ?? ""}
                </h6>
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("no")}
                  {": "}
                  {outward?.number ?? ""}
                </h6>
              </>
            ) : (
              <>
                {" "}
                <h6
                  style={{
                    marginBottom: "0.3rem",
                  }}
                >
                  {t("subject_only")}
                  {": "}
                  {outward?.subject ?? ""}
                </h6>
              </>
            )}
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("type")}
              {": "}
              {outward?.outward_type ?? ""}
            </h6>
            <h6
              style={{
                marginBottom: "0.3rem",
              }}
            >
              {t("prepared_by")}
              {": "}
              {`${outward?.prepare_by?.staffFirstName ?? ""} ${
                outward?.prepare_by?.staffMiddleName ?? ""
              } ${outward?.prepare_by?.staffLastName ?? ""}`}
            </h6>
            <p>
              {t("created_at")} :{" "}
              {dayjs(outward?.created_at)?.format("DD/MM/YYYY")} {", "}
              {localTimeFormat(outward?.created_at, "LT")}
            </p>
          </div>
        </div>
        <div
          className={style.sf_card_contatiner}
          style={{
            flexDirection: "column",
            alignItems: "flex-end",
          }}
        >
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onMenu}
            style={{
              height: "1.5rem",
              width: "1.5rem",
              cursor: "pointer",
            }}
          />
          {/* {outward?.generated_report ? (
            <a
              href={`${pdfCdnUrl}/${outward?.generated_report}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src={`${assestsUserFinanceUrl}/receipt.svg`}
                alt="receipt icon"
                style={{
                  width: "1.5rem",
                  height: "1.5rem",
                }}
              />
            </a>
          ) : null} */}

          <CreateButton
            label="approve"
            customStyle={{
              margin: "0",
              padding: "0.3rem 1rem",
            }}
            onAction={onApproved}
          />
          {outward?.cancle_by_staff?.includes(staffId) ? null : (
            <CreateButton
              label="reject"
              customStyle={{
                margin: "0",
                padding: "0.3rem 1rem",
              }}
              onAction={onReject}
            />
          )}
        </div>
      </Link>
      {disabled && <QvipleLoader />}
      {step === 1 ? (
        <InOutwardSentRequestPendingCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          menuData={outward}
        />
      ) : null}
    </>
  );
};

export default InOutwardReceiveRequestPendingCard;
