import { useTranslation } from "react-i18next";
import {
  useInoutwardRemoveInOutward,
  useInoutwardUpdateInOutward,
} from "../../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import AddInward from "../../Inward/AddInward";
import AddOutward from "../../Outward/AddOutward";
import { useState } from "react";

const InOutwardSentRequestPendingCardMenu = ({
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [inoutwardRemoveInOutward] = useInoutwardRemoveInOutward();
  const [inoutwardUpdateInOutward] = useInoutwardUpdateInOutward();
  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      inoutwardRemoveInOutward({
        oid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?._id) {
      onOpenEdit();
      setDisabled((pre) => !pre);
      inoutwardUpdateInOutward({
        oid: menuData?._id,
        bodyParameter: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {edit ? (
        <>
          {menuData?.module_type === "INWARD" ? (
            <AddInward
              isEdit
              onClose={onOpenEdit}
              onEdit={onEdit}
              menuData={menuData}
            />
          ) : (
            <AddOutward
              isEdit
              onClose={onOpenEdit}
              onEdit={onEdit}
              menuData={menuData}
            />
          )}
        </>
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onOpenEdit}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default InOutwardSentRequestPendingCardMenu;
