import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import OutwardCard from "./OutwardCard";
import { useInoutwardOutwardList } from "../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import AddOutward from "./AddOutward";
const Outward = ({ ioid, staffId, instituteId }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    inoutwardOutwardList,
    inoutwardOutwardListLoading,
    inoutwardOutwardListRefetch,
  } = useInoutwardOutwardList({
    data: {
      ioid: ioid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !ioid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (inoutwardOutwardList?.list?.length) {
      let obj = nextPage(inoutwardOutwardList?.list?.length);
      setState(() => obj);
    }
  }, [inoutwardOutwardList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !inoutwardOutwardListLoading &&
      inoutwardOutwardList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    inoutwardOutwardListRefetch();
  }, [inoutwardOutwardListRefetch]);

  const onCloseStep = () => {
    setStep("");
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("outward")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "320px",
                  }}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
            <CreateButton
              label="add"
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                width: "fit-content",
              }}
              onAction={() => setStep(1)}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {inoutwardOutwardList?.list?.map((outward, index) =>
          inoutwardOutwardList?.list?.length === index + 1 ? (
            <div key={outward?._id} ref={ref}>
              <OutwardCard outward={outward} />
            </div>
          ) : (
            <OutwardCard outward={outward} key={outward?._id} />
          )
        )}
        {inoutwardOutwardListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          inoutwardOutwardList?.list?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          inoutwardOutwardList?.list?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
      {step === 1 ? (
        <AddOutward
          instituteId={instituteId}
          staffId={staffId}
          onClose={onCloseStep}
          onRefetch={onRefetch}
          ioid={ioid}
        />
      ) : null}
    </>
  );
};

export default Outward;
