import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useEffect, useState } from "react";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import ReadyToPublishCard from "./ReadyToPublishCard";
import { useInoutwardReadyToPublishList } from "../../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
const ReadyToPublish = ({ ioid, staffId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { inoutwardReadyToPublishList, inoutwardReadyToPublishListLoading } =
    useInoutwardReadyToPublishList({
      data: {
        ioid: ioid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !ioid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (inoutwardReadyToPublishList?.list?.length) {
      let obj = nextPage(inoutwardReadyToPublishList?.list?.length);
      setState(() => obj);
    }
  }, [inoutwardReadyToPublishList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !inoutwardReadyToPublishListLoading &&
      inoutwardReadyToPublishList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
        </div>
      </UserMemberContentWrapper>

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {inoutwardReadyToPublishList?.list?.map((outward, index) =>
          inoutwardReadyToPublishList?.list?.length === index + 1 ? (
            <div key={outward?._id} ref={ref}>
              <ReadyToPublishCard outward={outward} />
            </div>
          ) : (
            <ReadyToPublishCard outward={outward} key={outward?._id} />
          )
        )}
        {inoutwardReadyToPublishListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          inoutwardReadyToPublishList?.list?.length === 0 && (
            <EmptyMessage title="No item found related this search." />
          )
        ) : (
          inoutwardReadyToPublishList?.list?.length === 0 && (
            <EmptyMessage title="No item." />
          )
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default ReadyToPublish;
